<template>
  <div class="wrapper">
    <CircleCue
      class="cue"
      :style="{
        top: windowHeight / 2 - 30 + 'px',
        'margin-left': windowWidth * 0.18 + 'px',
      }"
      :class="rightActive ? 'active' : 'hidden'"
      v-if="$root.isAnimated"
    />
    <ThreeCircles v-if="$root.isAnimated" />

    <div
      id="left"
      ref="left"
      @mouseover="shiftScreen('left')"
      :style="{ width: computedWidthLeft }"
    >
      <div class="box">
        <div
          class="rectangle"
          :class="leftActive ? 'active' : 'hidden'"
          :style="{ top: computedTopRect, 'margin-left': computedMarginsRect }"
          ref="rect"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            width="43%"
            height="83px"
          >
            <path
              fill-rule="evenodd"
              fill="rgb(255, 255, 255)"
              d="M0.0,0.0 L831.999,0.0 L831.999,83.0 L0.0,83.0 L0.0,0.0 Z"
            />
          </svg>
          <p class="text-cue">NAVIGATE THROUGH TABS · · · · · · ·</p>
        </div>
        <div
          class="text"
          :style="{ top: computedTopText, 'margin-left': computedMarginsText }"
          :class="rightActive ? 'active' : 'hidden'"
          ref="text"
        >
          <p>TO THE POINT.</p>
        </div>
      </div>
      <div
        class="menu"
        :class="leftActive ? 'active' : 'hidden'"
        :style="{ bottom: computedTopMenu, 'margin-left': computedMarginsRect }"
        ref="menu"
      >
        <a
          @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
          class="first hoverable"
          :class="activeTab == 0 ? 'tab-active' : ''"
          @click="setTab(0)"
          >INFO</a
        >
        <a
          @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
          class="second hoverable"
          :class="activeTab == 1 ? 'tab-active' : ''"
          @click="setTab(1)"
          >WORK</a
        >
        <a
          @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
          class="third hoverable"
          :class="activeTab == 2 ? 'tab-active' : ''"
          @click="setTab(2)"
          >TOS</a
        >
      </div>
      <TabContent
        :class="leftActive ? 'active' : 'hidden'"
        :style="{ top: computedTopTab, 'margin-left': computedMarginsRect }"
        :activeTab="activeTab"
        :previousTab="previousTab"
      />
      <div
        class="lower-menu"
        :style="{ top: computedTopLower, 'margin-left': computedMarginsMenu }"
        :class="leftActive ? 'active' : 'hidden'"
        ref="menu2"
      >
        <!-- <a
          @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
          class="hoverable"
          >CONTACT</a
        > -->
      </div>
    </div>
    <div
      id="right"
      ref="right"
      @mouseover="shiftScreen('right')"
      :style="{ width: computedWidthRight }"
    >
      <div id="magic" ref="magic"></div>

      <router-link to="/404"><div
        :style="{
          top: computedTopChevron,
          right: windowWidth * 0.125 + 154.2 / 2 + 'px',
        }"
        @mouseover="isHover(true)"
        @mouseleave="isHover(false)"
        class="button-chevron hoverable"
      >
        <svg
          id="chevron"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          width="76px"
          height="135px"
        >
          <path
            fill-rule="evenodd"
            fill="rgb(255, 255, 255)"
            d="M8.500,134.675 L0.14,126.189 L58.704,67.500 L0.14,8.810 L8.500,0.324 L75.675,67.500 L8.500,134.675 Z"
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          width="257px"
          height="257px"
        >
          <path
            fill-rule="evenodd"
            stroke="rgb(255, 255, 255)"
            stroke-width="10px"
            stroke-linecap="butt"
            stroke-linejoin="miter"
            fill="none"
            d="M126.0,5.0 C192.826,5.0 246.999,59.173 246.999,126.0 C246.999,192.826 192.826,246.999 126.0,246.999 C59.173,246.999 5.0,192.826 5.0,126.0 C5.0,59.173 59.173,5.0 126.0,5.0 Z"
          />
        </svg>
      </div></router-link>
      <div :style="{ top: computedTopPortfolio }" class="text">
        <p>PORTFOLIO.</p>
      </div>
    </div>
  </div>
</template>

<script>
import ThreeCircles from "../../components/threeCircles.vue";
import CircleCue from "../../components/circleCue.vue";
import TabContent from "./components/tabContent.vue";

export default {
  name: "HomeView",
  components: {
    ThreeCircles,
    CircleCue,
    TabContent,
  },
  data() {
    return {
      leftWidth: 0,
      rightWidth: 0,
      windowHeight: 0,
      windowWidth: 0,
      topPositions: {
        chevron: 0,
        text: 0,
        rectangle: 0,
        menu: 0,
        lowerMenu: 0,
        portfolio: 0,
      },
      margins: {
        rectLeft: 100,
        textLeft: 100,
        menuLeft: 100,
        chevronRight: 300,
      },
      leftActive: true,
      rightActive: false,
      activeTab: 0, // 0 - info, 1 - work, 2 - tos
      previousTab: null,
    };
  },
  computed: {
    computedWidthLeft: function () {
      return this.leftWidth + "px";
    },
    computedWidthRight: function () {
      return this.rightWidth + "px";
    },

    /// TOP POSITION PROPERTIES ///
    computedTopChevron: function () {
      return this.topPositions.chevron + "px";
    },
    computedTopText: function () {
      return this.topPositions.text + "px";
    },
    computedTopRect: function () {
      return this.topPositions.rectangle + "px";
    },
    computedTopMenu: function () {
      return this.windowHeight - this.topPositions.menu + "px";
    },
    computedTopTab: function () {
      return this.topPositions.rectangle + 100 + "px";
    },
    computedTopLower: function () {
      return this.topPositions.lowerMenu + "px";
    },
    computedTopPortfolio: function () {
      return this.topPositions.portfolio + "px";
    },

    /// MARGIN PROPERTIES ///
    computedMarginsRect: function () {
      return this.margins.rectLeft + "px";
    },
    computedMarginsText: function () {
      return this.margins.textLeft + "px";
    },
    computedMarginsMenu: function () {
      return this.margins.menuLeft + "px";
    },
    computedMarginsChevron: function () {
      return this.margins.chevronRight + "px";
    },
    /* computedMargin: function() {

    },
    computedOpacity: function (bool) {
        if (bool) {

        }
    } */
  },
  methods: {
    isHover(bool) {
      this.$root.$refs.cursor.expandCursor(bool);
    },
    leftIn() {
      this.leftWidth = this.windowWidth * 0.75;
      this.margins.rectLeft = 250;
      if (this.windowWidth < 1300) {
        let x = this.windowWidth;
        this.margins.rectLeft = -12495.78 + 35.44382 * x - 0.03313195 * x^2 + 0.00001034735 * x^3
      }
      this.margins.textLeft = 80;
    },
    leftOut() {
      this.leftWidth = this.windowWidth * 0.25;
      this.margins.rectLeft = 200;
      this.margins.textLeft = 0;
    },
    rightIn() {
      this.rightWidth = this.windowWidth * 0.75;
    },
    rightOut() {
      this.rightWidth = this.windowWidth * 0.25;
    },
    shiftScreen(direction) {
      if (this.windowWidth > 1300) {
        if (direction === "left") {
          this.leftIn();
          this.rightOut();
          this.leftActive = true;
          this.rightActive = false;
        } else if (direction === "right") {
          this.rightIn();
          this.leftOut();
          this.leftActive = false;
          this.rightActive = true;
        }
      }
    },
    updatePositionsAlt() {  
      this.leftWidth = this.windowWidth;
      this.rightWidth = this.windowWidth;
      let x = this.windowWidth;
      this.margins.rectLeft = 29196.51 - 120.6647*x + 0.1848139*x**2 - 0.0001241352*x**3 + 3.095111 / 10**8 * x**4
    }, 
    updatePositions() {
      this.topPositions.chevron = this.windowHeight / 2 - 154.2 / 2;
      this.topPositions.text = this.windowHeight / 2 - 160 / 2;
      this.topPositions.rectangle = this.windowHeight / 3;

      this.topPositions.menu = this.windowHeight / 3;
      this.topPositions.lowerMenu = this.windowHeight / 2 - 604 / 2 + 565;
      this.topPositions.portfolio = this.windowHeight / 2 - 132 / 2;
    },
    setTab(tab) {
      this.previousTab = this.activeTab;
      this.activeTab = tab; 
    },
  },
  beforeMount() {
    this.windowHeight = window.innerHeight;
    this.windowWidth = window.innerWidth;
    if (this.windowWidth > 1300) {
      this.leftWidth = this.windowWidth * 0.75;
      this.rightWidth = this.windowWidth * 0.25;
      this.shiftScreen("left");
    } else {
      this.leftWidth = this.windowWidth;
      this.rightWidth = this.windowWidth
      this.updatePositionsAlt()
    }
    
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
      this.updatePositions();
      if (this.windowWidth > 1300) {
         if (this.leftActive) {
          this.shiftScreen("left");
        } else {
          this.shiftScreen("right");
        }
      } else {
        if (this.rightActive) {
          this.leftIn();
          this.leftActive = true;
          this.rightActive = false;
        }
        this.updatePositionsAlt()
      }
    });
    this.updatePositions();
  },
  beforeUnmount() {},
};
</script>

<style scoped>
@media only screen and (max-width: 1300px) {
  .wrapper {
    flex-direction: column;
  }

  #left {
    height: 1080px;
  }
}

.active {
  opacity: 1;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.hidden {
  opacity: 0;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.active-transparent {
  opacity: 0.2;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.cue {
  position: absolute;
}

.cue2 {
  position: absolute;
  transform: scale(3);
  transform-origin: center;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
}

.wrapper p {
  margin: 0;
}

.wrapper #left {
  background-color: white;
  transition: all 1s ease-in-out;
  overflow: hidden;
  position: relative;
  z-index: 3;
  mix-blend-mode: difference;
}

.wrapper #left .box {
  width: inherit;
  color: white;
  mix-blend-mode: difference;
}

.wrapper #left .box .text {
  position: absolute;
  min-width: 553px;
  transform-origin: bottom;
  margin-left: 0;
  top: 40%;
  font-size: 72px;
  font-weight: 700;
  transform: rotate(-90deg);
  transition: margin-left 0.7s ease, opacity 0.5s ease;
}

.wrapper #left .box .rectangle {
  position: absolute;
  margin-left: 0;
  top: 27%;
  display: flex;
  width: 170%;
}

.text-cue {
  position: absolute;
  color: white;
  top: 35px;
  left: 54px;
  /* z-index: 5; */
  letter-spacing: 2px;
  font-size: 12px;
  mix-blend-mode: exclusion;
}

.wrapper #left .menu {
  position: absolute;
  align-items: flex-end;
  display: flex;
  color: white;
  text-align: left;
  mix-blend-mode: difference;
}

.wrapper #left .menu .first {
  font-size: 64px;
  font-weight: 800;
  left: 0;
  letter-spacing: -2px;
  margin-left: -5px;
  margin-right: 30px;
  /* text-shadow:
        -1px -1px 0 #fff,
        1px -1px 0 #fff,
        -1px 1px 0 #fff,
        1px 1px 0 #fff; */
  transition: all 0.5s ease;
}

.wrapper #left .menu .second {
  font-size: 64px;
  font-weight: 800;
  margin-right: 30px;
  left: 0;
  letter-spacing: -2px;
  transition: all 0.5s ease;
}

.wrapper #left .menu .third {
  font-size: 64px;
  font-weight: 800;
  min-width: 380px;
  left: 0;
  transition: all 0.5s ease;
}

.wrapper #left .menu .tab-active {
  font-size: 108px;
  transform: translateY(10px);
  color: white;
  transition: all 0.5s ease;
}

.first:visited,
.second:visited,
.third:visited {
  color: white;
  text-decoration: none !important;
}

.wrapper #left .lower-menu {
  position: absolute;
  font-size: 36px;
  font-weight: 800;
  color: white;
  text-align: left;
  mix-blend-mode: difference;
  left: 300px;
}

.wrapper #right {
  background-color: black;
  transition: all 1s ease-in-out;
  overflow: hidden;
  z-index: 2;
  position: relative;
  overflow: hidden;
  mix-blend-mode: difference;
}

.wrapper #right .button-chevron {
  position: absolute;
  right: 310px;
  transform: scale(0.6);
  mix-blend-mode: difference;
}

.wrapper #right .button-chevron #chevron {
  margin-top: 60px;
  margin-left: 100px;
}

.wrapper #right .button-chevron svg {
  position: absolute;
}

.wrapper #right .text {
  position: absolute;
  right: 500px;
  color: white;
  font-size: 108px;
  text-align: right;
  letter-spacing: 1px;
  mix-blend-mode: difference;
}
</style>